import axios from 'axios';

//   axios   
const api = axios.create({
  baseURL: process.env.REACT_APP_API_URL || 'http://localhost:8000',
  timeout: 300000, // 5    
  headers: {
    'Content-Type': 'application/json',
  },
});

//    
api.interceptors.response.use(
  (response) => response,
  (error) => {
    console.error('API Error:', error);
    
    if (error.response) {
      //     
      const message = error.response.data?.detail || error.response.data?.message || '  ';
      return Promise.reject(new Error(message));
    } else if (error.request) {
      //   ,    
      return Promise.reject(new Error('  .    .'));
    } else {
      // -      
      return Promise.reject(new Error('   '));
    }
  }
);

class ApiService {
  /**
   *  
   * @param {FormData} formData -  
   * @returns {Promise} -  
   */
  async uploadBook(formData) {
    try {
      const response = await api.post('/upload', formData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
          console.log(`Upload Progress: ${percentCompleted}%`);
        },
      });
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *   
   * @param {string} bookId - ID 
   * @param {string} splitMode -  
   * @param {number} numSections -  
   * @returns {Promise} -  
   */
  async analyzeStructure(bookId, splitMode = 'by_headings', numSections = 5) {
    try {
      const response = await api.post('/analyze', {
        book_id: bookId,
        split_mode: splitMode,
        num_sections: numSections,
      });
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *  PDF 
   * @param {string} bookId - ID 
   * @param {string} design -  
   * @returns {Promise} -  
   */
  async generatePDFs(bookId, design = 'classic_islamic') {
    try {
      const response = await api.post('/generate', {
        book_id: bookId,
        design: design,
      });
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *    
   * @returns {Promise} -  
   */
  async getAvailableDesigns() {
    try {
      const response = await api.get('/designs');
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *  PDF 
   * @param {string} bookId - ID 
   * @param {string} filename -  
   * @returns {Promise} - Blob 
   */
  async downloadPDF(bookId, filename) {
    try {
      const response = await api.get(`/download/${bookId}/${filename}`, {
        responseType: 'blob',
      });
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *   
   * @param {string} bookId - ID 
   * @returns {Promise} -  
   */
  async getGenerationStatus(bookId) {
    try {
      const response = await api.get(`/status/${bookId}`);
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *    
   * @returns {Promise} -  
   */
  async getAllGenerations() {
    try {
      const response = await api.get('/generations');
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *  
   * @param {string} bookId - ID 
   * @returns {Promise} -  
   */
  async deleteGeneration(bookId) {
    try {
      const response = await api.delete(`/generations/${bookId}`);
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *    
   * @param {string} bookId - ID 
   * @returns {Promise} -  
   */
  async getBookInfo(bookId) {
    try {
      const response = await api.get(`/books/${bookId}`);
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *   
   * @param {string} bookId - ID 
   * @returns {Promise} -  
   */
  async getBookStructure(bookId) {
    try {
      const response = await api.get(`/books/${bookId}/structure`);
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *   
   * @param {string} bookId - ID 
   * @param {Array} customSections -  
   * @param {string} design - 
   * @returns {Promise} -  
   */
  async createCustomSplit(bookId, customSections, design = 'classic_islamic') {
    try {
      const response = await api.post('/custom-split', {
        book_id: bookId,
        sections: customSections,
        design: design,
      });
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *   
   * @param {string} bookId - ID 
   * @param {Object} section -  
   * @returns {Promise} -  
   */
  async getSectionPreview(bookId, section) {
    try {
      const response = await api.post('/preview', {
        book_id: bookId,
        section: section,
      });
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *   API
   * @returns {Promise} -  
   */
  async healthCheck() {
    try {
      const response = await api.get('/');
      return response;
    } catch (error) {
      throw error;
    }
  }

  /**
   *   URL
   * @param {string} baseURL -  URL
   */
  setBaseURL(baseURL) {
    api.defaults.baseURL = baseURL;
  }

  /**
   *  
   * @param {number} timeout -   
   */
  setTimeout(timeout) {
    api.defaults.timeout = timeout;
  }
}

//     
export const apiService = new ApiService();

//    axios   
export { api };

export default apiService;
